.strength-meter {
  position: relative;
  height: 3px;
  background: #DDD;
  margin: 7px 0;
  border-radius: 2px; }
  .strength-meter:before, .strength-meter:after {
    content: '';
    height: inherit;
    background: transparent;
    display: block;
    border-color: #FFF;
    border-style: solid;
    border-width: 0 6px 0;
    position: absolute;
    width: calc(20% + 6px);
    z-index: 10; }
  .strength-meter:before {
    left: calc(20% - 3px); }
  .strength-meter:after {
    right: calc(20% - 3px); }

.strength-meter-fill {
  background: transparent;
  height: inherit;
  position: absolute;
  width: 0;
  border-radius: inherit;
  transition: width 0.5s ease-in-out, background 0.25s; }
  .strength-meter-fill[data-strength='0'] {
    width: 20%;
    background: darkred; }
  .strength-meter-fill[data-strength='1'] {
    width: 40%;
    background: orangered; }
  .strength-meter-fill[data-strength='2'] {
    width: 60%;
    background: orange; }
  .strength-meter-fill[data-strength='3'] {
    width: 80%;
    background: yellowgreen; }
  .strength-meter-fill[data-strength='4'] {
    width: 100%;
    background: green; }

.lds-ring-md {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px; }

.lds-ring-md div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: tomato transparent transparent transparent; }

.lds-ring-sm {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px; }

.lds-ring-sm div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 28px;
  height: 28px;
  margin: 6px;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: tomato transparent transparent transparent; }

.lds-ring-xs {
  display: inline-block;
  position: relative;
  width: 14px;
  height: 14px; }

.lds-ring-xs div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 10px;
  height: 10px;
  margin: 4px;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: tomato transparent transparent transparent; }

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
